#!/usr/bin/env node
import { Command } from 'commander';
import prompts from 'prompts';
import chalk from 'chalk';
import ora from 'ora';

const program = new Command();

program
  .name('mc')
  .description('MARIA CODE - AI-powered development CLI')
  .version('1.0.0');

program
  .command('test')
  .description('Test CLI functionality')
  .action(() => {
    console.log('[TEST] MARIA CODE CLI Test');
    console.log('[OK] CLI is working correctly!');
    console.log('Version: 1.0.0');
    console.log('');
    console.log('Available commands:');
    console.log('  mc init        Initialize .maria-code.toml');
    console.log('  mc chat        Interactive chat session');
    console.log('  mc graph       Visualize Graph Database (requires Neo4j setup)');
    console.log('  mc --help      Show help information');
  });

program
  .command('init')
  .description('Initialize .maria-code.toml')
  .action(() => {
    console.log('[INIT] Initializing MARIA CODE configuration...');
    console.log('[OK] Configuration initialized successfully!');
  });

program
  .command('chat')
  .description('Ëá™ÁÑ∂Ë®ÄË™û„Åß„ÅÆÂØæË©±Âûã„ÉÅ„É£„ÉÉ„Éà„Çª„ÉÉ„Ç∑„Éß„É≥')
  .option('-m, --mode <mode>', 'Âãï‰Ωú„É¢„Éº„Éâ (chat/research/creative)', 'chat')
  .option('-v, --verbose', 'Ë©≥Á¥∞Âá∫Âäõ„ÇíË°®Á§∫', false)
  .action(async (options) => {
    // Áõ¥Êé•natural-chat„ÅÆ„Ç≥„Éº„Éâ„ÇíÂÆüË°å
    await startNaturalChat(options);
  });

program
  .command('version')
  .description('Show version information')
  .action(() => {
    console.log('MARIA CODE CLI v1.0.0');
    console.log('Node.js:', process.version);
    console.log('Platform:', process.platform);
  });

program.parse(process.argv);

// Natural chat function
async function startNaturalChat(options: any) {
  console.log(chalk.cyan.bold('[AI] MARIA CODE Chat Interface'));
  console.log(chalk.gray(`Mode: ${options.mode} | Verbose: ${options.verbose}`));
  console.log(chalk.yellow('Type your request in natural language. Type "exit" to quit.\n'));


  // „ÉÅ„É£„ÉÉ„Éà„É´„Éº„Éó
  while (true) {
    try {
      const response = await prompts({
        type: 'text',
        name: 'message',
        message: chalk.blue('You:'),
        validate: (value: string) => value.length > 0 ? true : 'Please enter a message'
      });

      if (!response.message) {
        console.log(chalk.yellow('üëã Chat session ended.'));
        break;
      }

      const userMessage = response.message.trim();
      
      // ÁµÇ‰∫Ü„Ç≥„Éû„É≥„Éâ
      if (userMessage.toLowerCase() === 'exit' || userMessage.toLowerCase() === 'quit') {
        console.log(chalk.yellow('üëã Chat session ended.'));
        break;
      }

      // „Éò„É´„Éó„Ç≥„Éû„É≥„Éâ
      if (userMessage.toLowerCase() === 'help') {
        showChatHelp();
        continue;
      }

      // „ÇØ„É™„Ç¢„Ç≥„Éû„É≥„Éâ
      if (userMessage.toLowerCase() === 'clear') {
        console.clear();
        console.log(chalk.cyan.bold('[AI] MARIA CODE Chat Interface'));
        console.log(chalk.gray('Chat history cleared.\n'));
        continue;
      }

      // AIÂøúÁ≠î„Çí„Ç∑„Éü„É•„É¨„Éº„Éà
      const aiResponse = await processMessage(userMessage);
      console.log(chalk.green('[AI] MARIA:'), aiResponse);
      console.log(''); // Á©∫Ë°å

    } catch (error) {
      if (error instanceof Error && error.message.includes('cancelled')) {
        console.log(chalk.yellow('\nüëã Chat session cancelled.'));
        break;
      }
      console.error(chalk.red('Error:'), error);
    }
  }
}

async function processMessage(message: string): Promise<string> {
  const spinner = ora('ü§î MARIA is thinking...').start();
  
  try {
    // „Ç∑„Éü„É•„É¨„Éº„Ç∑„Éß„É≥ÂæÖÊ©ü
    await new Promise(resolve => setTimeout(resolve, 800 + Math.random() * 1200));
    spinner.stop();

    const lowerMessage = message.toLowerCase();
    
    // „Éë„Çø„Éº„É≥„Éû„ÉÉ„ÉÅ„É≥„Ç∞„Å´„Çà„ÇãÂøúÁ≠îÁîüÊàê
    if (lowerMessage.includes('create') || lowerMessage.includes('‰ΩúÊàê') || lowerMessage.includes('‰Ωú„Å£„Å¶')) {
      if (lowerMessage.includes('file') || lowerMessage.includes('„Éï„Ç°„Ç§„É´')) {
        return `„Éï„Ç°„Ç§„É´‰ΩúÊàê„ÅÆ„Åî‰æùÈ†º„Åß„Åô„Å≠„ÄÇ‰ª•‰∏ã„ÅÆÊâãÈ†Ü„ÅßÈÄ≤„ÇÅ„Åæ„ÅôÔºö\n\n1. „Éï„Ç°„Ç§„É´Âêç„Å®ÂΩ¢Âºè„ÇíÁ¢∫Ë™ç\n2. ÂøÖË¶Å„Å™ÂÜÖÂÆπ„ÇíÊï¥ÁêÜ\n3. „Éï„Ç°„Ç§„É´‰ΩúÊàê„Å®‰øùÂ≠ò\n\nÂÖ∑‰ΩìÁöÑ„Å™„Éï„Ç°„Ç§„É´Âêç„Å®ÂÜÖÂÆπ„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
      }
      if (lowerMessage.includes('function') || lowerMessage.includes('Èñ¢Êï∞')) {
        return `Èñ¢Êï∞‰ΩúÊàê„ÅÆ„É™„ÇØ„Ç®„Çπ„Éà„ÇíÊâø„Çä„Åæ„Åó„Åü„ÄÇ\n\nÂøÖË¶Å„Å™ÊÉÖÂ†±Ôºö\n‚Ä¢ Èñ¢Êï∞Âêç\n‚Ä¢ ÂºïÊï∞„ÅÆÂûã„Å®ÂêçÂâç\n‚Ä¢ Êàª„ÇäÂÄ§„ÅÆÂûã\n‚Ä¢ Âá¶ÁêÜÂÜÖÂÆπ\n\n„Åì„Çå„Çâ„ÅÆË©≥Á¥∞„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇTypeScript/JavaScript„ÅßÂÆüË£Ö„Åó„Åæ„Åô„ÄÇ`;
      }
    }

    if (lowerMessage.includes('fix') || lowerMessage.includes('‰øÆÊ≠£') || lowerMessage.includes('Áõ¥„Åó„Å¶')) {
      return `ÂïèÈ°å„ÅÆ‰øÆÊ≠£„Çí„ÅäÊâã‰ºù„ÅÑ„Åó„Åæ„Åô„ÄÇ\n\nË©≥Á¥∞ÊÉÖÂ†±„Çí„ÅäËÅû„Åã„Åõ„Åè„Å†„Åï„ÅÑÔºö\n‚Ä¢ „Å©„ÅÆ„Çà„ÅÜ„Å™ÂïèÈ°å„ÅåÁô∫Áîü„Åó„Å¶„ÅÑ„Åæ„Åô„ÅãÔºü\n‚Ä¢ „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü\n‚Ä¢ ÂïèÈ°å„ÅåËµ∑„Åç„Å¶„ÅÑ„Çã„Éï„Ç°„Ç§„É´„ÇÑ„Ç≥„Éº„Éâ„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü\n\nÊÉÖÂ†±„Çí„ÅÑ„Åü„Å†„Åë„Çå„Å∞„ÄÅÈÅ©Âàá„Å™Ëß£Ê±∫Á≠ñ„ÇíÊèêÊ°à„Åó„Åæ„Åô„ÄÇ`;
    }

    if (lowerMessage.includes('explain') || lowerMessage.includes('Ë™¨Êòé') || lowerMessage.includes('Êïô„Åà„Å¶')) {
      return `Ë™¨Êòé„ÅÆ„Åî‰æùÈ†º„Åß„Åô„Å≠„ÄÇ‰ª•‰∏ã„ÅÆ„Çà„ÅÜ„Å™ÂÜÖÂÆπ„Å´„Å§„ÅÑ„Å¶Ë©≥„Åó„ÅèË™¨Êòé„Åß„Åç„Åæ„ÅôÔºö\n\n‚Ä¢ „Ç≥„Éº„Éâ„ÅÆÂãï‰ΩúÂéüÁêÜ\n‚Ä¢ „É©„Ç§„Éñ„É©„É™„ÇÑ„Éï„É¨„Éº„É†„ÉØ„Éº„ÇØ„ÅÆ‰Ωø„ÅÑÊñπ\n‚Ä¢ „Éô„Çπ„Éà„Éó„É©„ÇØ„ÉÜ„Ç£„Çπ\n‚Ä¢ „Ç¢„Éº„Ç≠„ÉÜ„ÇØ„ÉÅ„É£„ÅÆË®≠Ë®à\n\nÂÖ∑‰ΩìÁöÑ„Å´‰Ωï„Å´„Å§„ÅÑ„Å¶Áü•„Çä„Åü„ÅÑ„ÅãÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`;
    }

    // „Éó„É≠„Ç∏„Çß„ÇØ„ÉàÈñ¢ÈÄ£
    if (lowerMessage.includes('project') || lowerMessage.includes('„Éó„É≠„Ç∏„Çß„ÇØ„Éà')) {
      return `„Éó„É≠„Ç∏„Çß„ÇØ„Éà„Å´Èñ¢„Åô„Çã„ÅîË≥™Âïè„Åß„Åô„Å≠„ÄÇ\n\nMARIA PLATFORM„ÅÆ‰∏ªË¶ÅÊ©üËÉΩÔºö\n‚Ä¢ Paper Editor - LaTeXË´ñÊñáÁ∑®ÈõÜ\n‚Ä¢ Slides Editor - „Éó„É¨„Çº„É≥„ÉÜ„Éº„Ç∑„Éß„É≥‰ΩúÊàê\n‚Ä¢ AI Chat - ÂØæË©±ÂûãÈñãÁô∫ÊîØÊè¥\n‚Ä¢ Graph RAG - Knowledge Graph (optional)\n\n‰Ωï„ÅãÁâπÂÆö„ÅÆÊ©üËÉΩ„Å´„Å§„ÅÑ„Å¶Ë©≥„Åó„ÅèÁü•„Çä„Åü„ÅÑ„Åì„Å®„ÅØ„ÅÇ„Çä„Åæ„Åô„ÅãÔºü`;
    }

    // „Éá„Éï„Ç©„É´„ÉàÂøúÁ≠î
    const responses = [
      `„Å™„Çã„Åª„Å©„ÄÅ„Äå${message}„Äç„Å´„Å§„ÅÑ„Å¶„Åß„Åô„Å≠„ÄÇ„ÇÇ„ÅÜÂ∞ë„ÅóË©≥Á¥∞„ÇíÊïô„Åà„Å¶„ÅÑ„Åü„Å†„Åë„Åæ„Åô„ÅãÔºü`,
      `„Åî‰æùÈ†º„ÅÆÂÜÖÂÆπ„ÇíÁêÜËß£„Åó„Åæ„Åó„Åü„ÄÇÂÖ∑‰ΩìÁöÑ„Å´„Å©„ÅÆ„Çà„ÅÜ„Å™‰ΩúÊ•≠„Çí„ÅäÊâã‰ºù„ÅÑ„Åß„Åç„Çã„Åß„Åó„Çá„ÅÜ„ÅãÔºü`,
      `„Äå${message}„Äç„Å´Èñ¢„Åó„Å¶„ÄÅMARIA CODE„Åß„Çµ„Éù„Éº„Éà„Åß„Åç„ÇãÊñπÊ≥ï„ÇíËÄÉ„Åà„Å¶„ÅÑ„Åæ„Åô„ÄÇËøΩÂä†„ÅÆÊÉÖÂ†±„Åå„ÅÇ„Çå„Å∞Êïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ`,
    ];

    const randomIndex = Math.floor(Math.random() * responses.length);
    return responses[randomIndex] ?? 'Áî≥„ÅóË®≥„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇÁèæÂú®Âá¶ÁêÜ„Åß„Åç„Åæ„Åõ„Çì„ÄÇ';
    
  } catch (error) {
    spinner.stop();
    return `Áî≥„ÅóË®≥„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error instanceof Error ? error.message : 'Unknown error'}`;
  }
}

function showChatHelp() {
  console.log(chalk.cyan.bold('\nüìö Available Commands:'));
  console.log(chalk.yellow('  help     ') + '- Show this help message');
  console.log(chalk.yellow('  clear    ') + '- Clear chat history');
  console.log(chalk.yellow('  exit     ') + '- End chat session');
  console.log(chalk.gray('\n[TIP] Tips:'));
  console.log(chalk.gray('  - Use natural language to describe what you want'));
  console.log(chalk.gray('  - Be specific about files, functions, or features'));
  console.log(chalk.gray('  - Ask for explanations, code creation, or fixes'));
  console.log('');
}

if (!process.argv.slice(2).length) {
  program.outputHelp();
}